
import store from '@/store/index';
import { mapActions } from 'vuex';
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Loading from 'vux/src/components/loading/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import srcFloor1 from './../assets/images/floor.png';
export default {
name: 'HelloWorld',
directives: {
TransferDom
},
data () {
return {
srcFloor: srcFloor1,
list2: ['Map', 'Events', 'Information'],
index2: 0,
currentIndex: 0,
currentPage: 1,
show2: false,
demo2: 'Buildings of Schools',
onFetching: false
};
},
methods: {
...mapActions(['loadPageConfig']),
changeRouter () {
this.$router.push({ path: '/index' });
},
changePage (val) {
let _this = this;
this.show2 = true;
setTimeout(() => {
_this.show2 = false;
_this.currentPage = val;
}, 1000);
},
onScrollBottom () {
if (this.onFetching) {

} else {
this.onFetching = true;
setTimeout(() => {
this.bottomCount = Array.concat(this.bottomCount, this.addData);
this.onFetching = false;
}, 2000);
}
},
preImg () {
if (this.currentIndex === 0) {
this.currentIndex = 2;
} else {
this.currentIndex--;
}
},
nextImg () {
if (this.currentIndex === 2) {
this.currentIndex = 0;
} else {
this.currentIndex++;
}
}
},
components: {
XHeader,
Tab,
TabItem,
Flexbox,
FlexboxItem,
XImg,
Swiper,
SwiperItem,
Scroller,
LoadMore,
Group,
Loading
},
store
};



